import styled from 'styled-components'

export const BalanceWrapper = styled.div`
    width: 400px;
    height: 120px;
    border: 1px solid black;
    border-radius: 10px;
    margin-bottom: 30px;
    position: relative;

    >div {
        height: 100%;
    }

    ::after {
        content: '';
        top: 50px;
        right: -11px;
        border: 2px solid gray;
        border-left: none;
        width: 10px;
        height: 20px;
        position: absolute;
    }
`;