import React, {Component} from 'react';
import { Row, Col } from 'reactstrap';
import { EventContainer } from '../experience/styled'
import JouleIcon from '../../assets/green-energy.png'

export default class Experience extends Component {
    state = {
        confirmed: {}
    }
    render() {
        return (
            <Row>
                {mockup.map((product, index) => 
                    <Col key={ product.date + product.start_time + index } xs='12' sm='12' md='6' lg='4'>
                        <EventContainer style={{backgroundImage: `url(${product.img})`, marginBottom: '10px'}} />
                        <div style={{display: 'flex', alignItems: 'center', flexDirection: 'column'}} >
                            <div style={{backgroundColor: '#cbcbcb', border: '1px black solid', borderRadius: '6px', padding: '4px 7px', minWidth: '50px', marginBottom: '10px', display: 'flex', fontSize: '12px', fontWeight: 800,justifyContent: 'center'}}>{product.name}</div>
                            <div style={{backgroundColor: '#cbcbcb', border: '1px black solid', borderRadius: '10px', padding: '4px 7px', minWidth: '125px', marginBottom: '10px', display: 'flex', fontSize: '14px', fontWeight: 800, justifyContent: 'space-around', alignItems: 'center'}}>
                                <img style={{height: '30px'}} src={ JouleIcon } alt=''/> 
                                <div style={{textAlign: "center"}} >
                                    <p style={{marginBottom: 0}} >{product.price}</p> joules
                                </div>
                            </div>
                        </div>
                    </Col>
                )}
            </Row>
        )
    }
}

